import {
	myRequestGet
} from '@/utils/request.js';
export default {
	data() {
		return {
			swipers: {},
			swiperss: {},
			swipersss: {},
			swiperssss: {},
			shj: {},
			shjj: {}
		};
	},
	created() {
		this.getSwipers();
		//console.log('组件6加载完毕');
		this.getSwiperss();
		this.getSwipersss();
		this.getSwiperssss();
		this.getshj();
		this.getshjj()
	},
	methods: {
		async getSwipers() {
			const res = await myRequestGet('/api/v1/fatiao/index/list?id=6&p=1&notchannel=27&order=1&limit=1&channel_id=6')
			this.swipers = res
		},
		async getSwiperss() {
			const ress = await myRequestGet('/api/v1/fatiao/article/list?id=6&p=1&notchannel=27&order=1&limit=6&channel_id=6')
			this.swiperss = ress
		},
		async getSwipersss() {
			const resss = await myRequestGet('/api/v1/fatiao/index/list?id=6&p=2&notchannel=27&order=1&limit=1&channel_id=6')
			this.swipersss = resss
		},
		async getSwiperssss() {
			const ressss = await myRequestGet('/api/v1/fatiao/article/list?id=6&p=2&notchannel=27&order=1&limit=6&channel_id=6')
			this.swiperssss = ressss
		},
		async getshj() {
			const re = await myRequestGet('/api/v1/fatiao/index/list?id=6&p=3&notchannel=27&order=1&limit=1&channel_id=6')
			this.shj = re
		},
		async getshjj() {
			const rew = await myRequestGet('/api/v1/fatiao/article/list?id=6&p=3&notchannel=27&order=1&limit=6&channel_id=6')
			this.shjj = rew
		}
	}
};
